 
import React from "react";
import StatusBadge from "./StatusBadge";
import EditButton from "./EditButton";

interface Soutenance {
  id: number;
  date: string;
  time: string;
  location: string;
  groupe: string;
  juryNames: string | string[];  // Corrected to handle string or string[] for jury names
  status: string;
}

interface SoutenanceTableProps {
  soutenances: Soutenance[];
}

const SoutenanceTable = ({ soutenances }: SoutenanceTableProps) => {
  console.log("Rendering soutenances in table:", soutenances);  // Log the soutenances data passed into the table

  return (
    <div className="overflow-x-auto">
      <table className="min-w-full bg-white rounded-lg shadow-md">
        <thead>
          <tr className="bg-gray-100">
            <th className="px-6 py-3 text-left text-sm font-semibold text-gray-700">
              Date
            </th>
            <th className="px-6 py-3 text-left text-sm font-semibold text-gray-700">
              Heure
            </th>
            <th className="px-6 py-3 text-left text-sm font-semibold text-gray-700">
              Lieu
            </th>
            <th className="px-6 py-3 text-left text-sm font-semibold text-gray-700">
              Groupe
            </th>
            <th className="px-6 py-3 text-left text-sm font-semibold text-gray-700">
              Jury
            </th>
            <th className="px-6 py-3 text-left text-sm font-semibold text-gray-700">
              Statut
            </th>
            <th className="px-6 py-3 text-left text-sm font-semibold text-gray-700">
              Actions
            </th>
          </tr>
        </thead>
        <tbody className="divide-y divide-gray-200">
          {soutenances.map((soutenance) => (
            <tr
              key={soutenance.id}
              className="hover:bg-gray-50 transition-colors"
            >
              <td className="px-6 py-4 text-sm text-gray-700">
                {new Date(soutenance.date).toLocaleDateString("fr-FR", {
                  day: "numeric",
                  month: "long",
                  year: "numeric",
                })}
              </td>
              <td className="px-6 py-4 text-sm text-gray-700">
                {soutenance.time}
              </td>
              <td className="px-6 py-4 text-sm text-gray-700">
                {soutenance.location}
              </td>
              <td className="px-6 py-4 text-sm text-gray-700">
                {soutenance.groupe}
              </td>
              <td className="px-6 py-4 text-sm text-gray-700">
                {Array.isArray(soutenance.juryNames)
                  ? soutenance.juryNames.join("   ,  ")  // Join the names with commas
                  : soutenance.juryNames}            
              </td>

              <td className="px-6 py-4 text-sm">
                <StatusBadge status={soutenance.status} />
              </td>
              <td className="px-6 py-4 text-sm">
                <EditButton soutenanceId={soutenance.id} />
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default SoutenanceTable;
